import React from "react";
import Local from "next/font/local";
import ReactPlayer from "react-player";
import { AiOutlineExclamationCircle } from "react-icons/ai";
import Image from "next/image";

const heroes = Local({
  src: [
    {
      path: "../../../public/fonts/Heroes Legend.ttf",
      weight: "400",
      style: "normal",
    },
  ],
  variable: "--font-heroes-legend",
  display: "swap",
});



interface Project {
  projectId: string;
  buttonUrl?: string;
  videoUrl?: string;
  title: string;
  thumbnail: string;
  description: string;
  shortDescription?: string;
  link?: undefined;
  linkText?: undefined;
  date: string;
  lastUpdated?: undefined;
}

const DescriptionBg = "https://res.cloudinary.com/dmmzqpfgg/image/upload/v1749833128/orange-comic-background_692379-312_xvpzbm.jpg";

const ProjectDetails = ({ project }: { project: Project }) => {
  return (
    <div className="w-full min-h-screen">
      <div className="w-full h-[23rem] flex flex-col space-y-3">
        <div className="py-2 w-full mt-6">
          <h1
            className={`text-xl tracking-wide text-shadow-2xs leading-9 text-shadow-black text-amber-300 font-bold w-full text-center ${heroes.className}`}>
            {project.title}
          </h1>
        </div>
        <div className="w-full px-3 flex flex-row justify-between font-semibold text-amber-300 text-shadow-2xs text-shadow-black">
          <p>{project.lastUpdated}</p>
          <p>{project.date}</p>
        </div>
        <div className="w-full flex items-start space-x-1 font-semibold tracking-wider opacity-60 px-5">
          <AiOutlineExclamationCircle size={15} />
          <p className="text-sm w-full leading-4">
            Note: These videos were made just for fun with my batchmates.
          </p>
        </div>
        <div className="w-full h-[11.3rem] rounded-2xl px-2">
          <ReactPlayer
            url={project.videoUrl}
            width="100%"
            height="100%"
            controls
            playing={false}
            light={project.thumbnail}
          />
        </div>
      </div>
      <div className="w-full rounded-lg relative flex flex-col items-center justify-center">
        <p className={`absolute z-20 px-4`}>
          {project.description}
        </p>
        <div className="h-[11.3rem] w-full relative">
          <Image src={DescriptionBg} alt="comic style bg" fill />
</div>
      </div>
    </div>
  );
};

export default ProjectDetails;
